.main {
  background-color: #F5F5F5;
  height: 470px;
  max-width: 800px;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
  margin-bottom: 40px;
}
.roll {
  height: 50px;
  width: 150px;
  border: none;
  border-radius: 6px;
  background-color: #5035FF;
  color: white;
  font-size: 1.2rem;
  font-family: 'Karla', sans-serif;
  cursor: pointer;
}
.roll:focus {
  outline: none;
}
.roll:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}
.title {
  font-size: 40px;
  margin: 0;
}
.instructions {
  text-align: center;
  max-width: 450px;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  margin: 0 50px 35px 50px;
}